import { createGlobalStyle } from "styled-components";

const GlobalStyles = createGlobalStyle`
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    &::-webkit-scrollbar{
        width: 2rem;
    }
    &::-webkit-slider-thumb{
        background-color: darkgray;
    }
}
body{
    font-family: 'Montserrat', sans-serif;
    width: 100%;
}
h2{
    font-size: 3rem;
    font-family: 'Abril Fatface', crusive;
    font-weight: lighter;
}
h3{
    font-size: 1.3rem;
    color: #333;
    padding: 2rem 0rem;
}
p{
    font-size: 1.2rem;
    line-height: 200%;
    color: #696969;
}
a{
    text-decoration: none;
    color: #333;
}
img{
    display: block;
}
input {
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
}

@media screen and (min-width: 360px) and (max-width: 420px) {
    h2{
        font-size: 1.6rem;
    }
    p{
        font-size: 0.8rem;
        line-height: 1.3rem;
    }
    h3{
        font-size: 1rem;
    }
}

`

export default GlobalStyles;